bbody {
  margin: 0;
  font-family: Arial, sans-serif;
  /* Metallic royal blue gradient */
  background: linear-gradient(
    135deg,
    #0a1d4d 0%,
    #1e3a8a 30%,
    #274690 60%,
    #3b82f6 100%
  );
  color: white;
  min-height: 100vh;
}

body::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: repeating-linear-gradient(
    90deg,
    rgba(255, 255, 255, 0.08) 0px,
    rgba(255, 255, 255, 0.08) 1px,
    transparent 1px,
    transparent 3px
  );
  pointer-events: none;
}

.header {
  position: sticky;
  top: 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  background: #0a1a33;
  border-bottom: 2px groove #444;
}

.logo {
  font-size: 1.5rem;
  font-weight: bold;
}

.nav {
  display: flex;
  gap: 1rem;
}

.nav a {
  text-decoration: underline;
  color: #d3d3d3;
}

.hamburger {
  display: none;
  font-size: 1.5rem;
  background: none;
  border: none;
  color: #d3d3d3;
}

/* Landing */
.landing {
  text-align: center;
  padding: 3rem 1rem;
}

.categories {
  display: flex;
  gap: 1rem;
  margin-top: 2rem;
}

.category {
  flex: 1;
  padding: 2rem;
  background: #112244;
  border-radius: 8px;
  cursor: pointer;
}

/* Responsive */
@media (max-width: 768px) {
  .nav {
    display: none;
  }

  .hamburger {
    display: block;
  }

  .categories {
    flex-direction: column;
  }
}
